// 路由懒加载 https://vuefe.cn/vue-router/advanced/lazy-loading.html
import List from './views/list.vue'
import Create from './views/create.vue'

export default [
  {
   path: '/',
   redirect: '/list',
  },
  {
    path: '/list',
    component: List,
  },{
    path: '/create',
    component: Create,
  }
];



